<template>
  <div class="cart-count">
      <div class="count-wrapper">
          <check-ed :is-checked="allchecked"  @click.native="checkallbtn"></check-ed><span>全选</span>
      </div>
      <div class="count-submit">
          <span class="count-num">合计<b>{{totalPrice}}</b>元</span>
          <span class="submit-btn">结算({{checkedLength}})</span>
      </div>
  </div>
</template>

<script>
//外部组件
import CheckEd from "components/common/check/CheckEd";
import { mapGetters } from "vuex";
export default {
  name: "CartCount",
  components:{
      CheckEd
  },
  computed:{
       ...mapGetters(['cartlistdata']),
       // 计算购物车总价(pre是上一次调用回调返回的值)和(item作为最新调用 callback 的第一个参数)
        totalPrice() {
            return '¥' + this.cartlistdata
                .filter(item => item.checked)
                .reduce((pre, item) => pre + item.price * item.count, 0)
                .toFixed(2);
        },

        //计算有多少个商品需要结算
        checkedLength() {
            return this.cartlistdata.filter(item => item.checked).length;
        },

        //计算点击商品状态时候在全选状态的改变
        allchecked(){
            if(this.cartlistdata.length === 0 ) return false
            //1、使用find
            //return !this.cartlistdata.find(item => !item.checked) 
            //2、使用filter
            //return !(this.cartlistdata.filter(item => !item.checked).length) 性能不友好
            //3、for
            for(let item of this.cartlistdata){
                //如果不选中的话
                if(!item.checked){
                    return false
                }
            }
            return true
            
        }
  },
  methods:{
      //点击全选按钮
      checkallbtn(){
          if(this.allchecked){ //拿到计算allchecked来判断，如果全部选中状态
                this.cartlistdata.forEach(item => item.checked = false)
          }else{
                this.cartlistdata.forEach(item => item.checked = true)
          }

      }
  }

};
</script>
<style lang="scss" scoped>
.cart-count{
    position: fixed;
    bottom: 43px;
    bottom: calc(43px + constant(safe-area-inset-bottom));
    bottom: calc(43px + env(safe-area-inset-bottom));
    width: 100%;
    max-width: 768px;
    z-index: 999;
    display: flex;
    -webkit-box-pack: justify;
    -webkit-justify-content: space-between;
    justify-content: space-between;
    padding: 0 12px;
    background-color: #FFFFFF;
    height: 54px;
    -webkit-box-align: center;
    -webkit-align-items: center;
    align-items: center;
    .count-wrapper{
        white-space: nowrap;
        display: flex;
        -webkit-box-align: center;
        -webkit-align-items: center;
        align-items: center;
        margin-right: 10px;
    }
    .count-submit{
        display: flex;
        -webkit-box-align: center;
        -webkit-align-items: center;
        align-items: center;
        -webkit-box-pack: end;
        -webkit-justify-content: flex-end;
        justify-content: flex-end;
        overflow: hidden;
        .submit-btn{
            background: linear-gradient(90deg,#27a6fa,#8e71f5);
            background: -webkit-linear-gradient(left,#27a6fa,#8e71f5);
            font-size: 15px;
            line-height: 15px;
            padding: 8px 18px;
            text-align: center;
            border-radius: 80px;
            color: #FFFFFF;
            white-space: nowrap;
            margin-left: 10px;
        }
        .count-num{
            font-size:14px;
            b{
                color:red;
            }

        }
    }
}
</style>