<template>
  <div class="my-cart">
     <!--头部--> 
    <head-menu class="cart-header">
     <div slot="center">购物车({{addCartLength}})</div>
    </head-menu>
     <!--购物车--> 
     <cart-num v-for="(item, index) in cartlistdata" :key="index" :iteminfo="item"></cart-num>
     <!--去结算--> 
     <cart-count></cart-count>
  </div>

</template>

<script>
//内部引用
import CartNum from "views/cart/modular/CartNum";
import CartCount from "views/cart/modular/CartCount";
//外部组件
import HeadMenu from "components/common/headmenu/HeadMenu";
import { mapGetters } from "vuex";
export default {
  name: "MyCart",
  components: {
      HeadMenu,
      CartNum,
      CartCount
  },
  computed: {
    // 对象写法可以自己命名
    // ...mapGetters({
    //   cartLength: "addCartLength"
    // })
    ...mapGetters(['addCartLength', 'cartlistdata'])//第二种方法
  }

};
</script>
<style lang="scss" scoped>
.my-cart{
    background-color: rgb(242 242 242);
     height: calc(100vh -50px);
    .cart-header{
        background: linear-gradient(90deg,#27a6fa,#8e71f5);
        background: -webkit-linear-gradient(left,#27a6fa,#8e71f5);
        div{
            text-align: center;
            color: #fff;
            font-size: 16px;
        }
    }
}
</style>